import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';

import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';


import { Root, View, Panel,PanelHeader,Group,CellButton } from '@vkontakte/vkui/dist';


class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activeView: 'view1'
		}
	}

	render() {
		return (
			<Root activeView={this.state.activeView}>
				<View activePanel="panel1.1" id="view1">
					<Panel id="panel1.1">
						<PanelHeader>Каталог бесед 1</PanelHeader>
						<Group>
							<CellButton onActionClick={() => this.setState({ text: 'Добавляем метку.' })}>
								
								Open View 2
							</CellButton>
						</Group>
					</Panel>
				</View>
				<View header activePanel="panel2.1" id="view2">
					<Panel id="panel2.1">
						<PanelHeader>View 2</PanelHeader>
						<Group>
							<CellButton onClick={ () => this.setState({ activeView: 'view1' }) }>
								Back to View 1
							</CellButton>
						</Group>
					</Panel>
				</View>
			</Root>
		)
	}
}



export default App;

